@charset "UTF-8";
:root {
  --blue-color: #2563EB;
  --blue-rgb-color: 37,
  99,
  235;
  --amber-color: #FBBF24;
  --amber-rgb-color: 251,
  191,
  36;
  --amerald-color: #22C55E;
  --amerald-rgb-color: 34,
  197,
  94;
  --red-color: #EF4444;
  --red-rgb-color: 239,
  68,
  68;
  --dark-color: #0F172A;
  --dark-rgb-color: 15,
  23,
  42;
  --light-color: #CBD5E1;
  --light-rgb-color: 203,
  213,
  225;
  --cyan-color: #22D3EE;
  --cyan-rgb-color: 34,
  211,
  238;
  --lime-color: #A3E635;
  --lime-rgb-color: 163,
  230,
  53;
  --violet-color: #8B5CF6;
  --violet-rgb-color: 139,
  92,
  246;
  --purple-color: #A855F7;
  --purple-rgb-color: 168,
  85,
  247;
  --rose-color: #F43F5E;
  --rose-rgb-color: 244,
  63,
  94;
  --orange-color: #F97316;
  --orange-rgb-color: 249,
  115,
  22;
  --main-color: var(--amerald-color);
  --main-rgb-color: var(--amerald-rgb-color);
  --text-main: var(--amerald-color);
  --border-radius-size: 4px;
  --border-color: #CBD5E1;
}

html,
body {
  scroll-behavior: auto !important;
  height: auto;
}

body {
  font-size: 13px;
  font-family: "Poppins", "Kanit", "Changa", sans-serif;
  font-weight: 500;
  color: var(--dark-color);
  background-color: #fff;
}

a {
  color: inherit;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

span {
  display: inline-block;
}

button {
  background-color: transparent;
  border: none;
}

.bx {
  font-size: 18px;
  color: inherit;
}

.section {
  padding: 60px 0;
}
.section .subtitle {
  font-size: 16px;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin-bottom: 10px;
  font-weight: 300;
}
.section .title {
  font-size: 36px;
  font-weight: 600;
  font-family: "Kanit", sans-serif;
  text-transform: capitalize;
  margin-bottom: 32px;
}
@media (max-width: 992px) {
  .section .title {
    margin-bottom: 24px;
    font-size: 28px;
  }
}
.section p {
  font-size: 15px;
  line-height: 25px;
  margin-bottom: 16px;
  color: #475569;
  font-weight: 400;
}
.section-image {
  position: relative;
}

body::-webkit-scrollbar {
  width: 8px;
}

body::-webkit-scrollbar-thumb {
  background-color: rgba(var(--main-rgb-color), 0.5);
  border-radius: 4px;
}

body::-webkit-scrollbar-thumb:hover {
  background-color: var(--main-color);
}

@keyframes moveUpDown {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(30px);
  }
}
@keyframes moveLeftRight {
  0%, 100% {
    transform: translateX(0);
  }
  50% {
    transform: translateX(30px);
  }
}
@keyframes rotateScale {
  0%, 100% {
    transform: rotate(0deg) scale(1);
  }
  50% {
    transform: rotate(15deg) scale(1.1);
  }
}
@keyframes diagonalMove {
  0%, 100% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(20px, -20px);
  }
}
.v-animate {
  animation-duration: 5s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}

.move-up-down {
  animation: moveUpDown 5s infinite ease-in-out;
}

.move-left-right {
  animation: moveLeftRight 5s infinite ease-in-out;
}

.rotate-scale {
  animation: rotateScale 5s infinite ease-in-out;
}

.diagonal-move {
  animation: diagonalMove 5s infinite ease-in-out;
}

.form-control {
  border-radius: 0;
  font-size: inherit;
}
.form-control:focus {
  outline: none;
  border-color: var(--main-color);
  box-shadow: none;
}

.dropdown-menu {
  font-size: inherit;
  border: none;
  border-radius: 0.25rem;
  box-shadow: 0 0 1.0625rem rgba(0, 0, 0, 0.1);
}

