}
.hero-image::before {
  position: absolute;
  content: "";
  width: clamp(320px, 40vw, 500px);
  height: clamp(320px, 40vw, 500px);
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0.5), transparent);
  border-radius: 50%;
  right: 0;
  z-index: -1;
  top: 0;
}

.abs {
  position: absolute;
  pointer-events: none;
  transition: transform 0.1s linear;
}
.abs.book {
  left: 54%;
  width: 100px;
  bottom: 80px;
  animation-name: rotateScale;
}
.abs.circle {
  top: -20px;
  z-index: -1;
  opacity: 0.6;
  left: -20px;
}
.abs.red-plus {
  top: 40%;
  left: 52%;
  animation-name: moveLeftRight;
}
.abs.crown {
  top: 50%;
  right: 140px;
  z-index: 1;
  animation-name: diagonalMove;
}
.abs.dots {
  top: 152px;
  right: 12%;
  z-index: -1;
  animation-name: moveUpDown;
}
@media (max-width: 992px) {
  .abs.dots {
    top: 25%;
    right: 30px;
    z-index: -1;
  }
  .abs.book, .abs.crown {
    display: none;
  }
  .abs.red-plus {
    top: 65%;
    left: 5%;
    z-index: 1;
  }
  .abs.circle {
    top: -20px;
    width: 45%;
    left: -20px;
  }
}

