.brand-slider {
    .swiper-slide {
        width: 200px;
        text-align: center;
    }
    
    .swiper-wrapper {
        display: flex;
        align-items: center;
        width: max-content;
        animation: scrollLeft 100s linear infinite;
    }
}

@keyframes scrollLeft {
    from {
        transform: translateX(0);
    }
    to {
        transform: translateX(-100%);
    }
}